body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
/*  background-image: url('assets/img/pexels-anni-roenkae-2156881.jpg');*/

  background-size: 100% 100%;
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-color: white;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.MuiPaper-rounded {
  border: 1px solid black;
}

.hideSmall {
  display: none;
}

@media only screen and (min-width: 600px) {
  .minHeight {
    min-height: 1026px;
  }
  .minHeight-small {
    min-height: 505px;
  }

}

@media only screen and (min-width: 1100px) {

  .hideLarge {
    display: none;
  }

  .hideSmall {
    display: unset;
  }

  .paddingLeft_MainPanel {
    padding-left: 48px!important;
  }
}

.flex-middle-center{ 
  Display: flex;
  Align-Items: center;
  Justify-Content: space-evenly;
  }

  .truncate-text {
    width: 100%;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }

  .countdown {
    font-size: xx-large;
  }

  .MuiPaper-rounded {
    /*opacity: 0.88;*/
    background: rgb(38,35,53)!important;
    background: linear-gradient(180deg, rgb(31, 39, 54) 0%, rgb(28, 37, 51) 35%, rgb(25, 34, 53) 100%)!important;
  }

a {
  color: white;
}
